// Variables
$card-border-color: $base-border-color;
$card-border: 1px solid $card-border-color;
$card-background: lighten($card-border-color, 10%);
$card-header-background: $card-background;
$card-margin: 1em;
$card-image-hover-opacity: 0.7;
$card-image-hover-color: $light-gray;

.container {
  @include outer-container;

  .header {
    @include row;
    h1 {
      font-size: $base-font-size * 2;
      line-height: $base-line-height * 0.7;
      text-transform: uppercase;
      margin-bottom: 0.25em;
      color: $base-font-color; } }

  .decoration-element {
    width: calc(100% - 1em);
    height: 15px;
    display: block;
    margin-bottom: 1.5rem;
    background-image: linear-gradient(to right, $base-font-color 1px, transparent 0%);
    background-size: 3px 100%; }

  .news-items {
    @include display(flex);
    @include flex-wrap(wrap);
    @include justify-content(space-between);
    @include align-content(space-between);

    .news-item {
      @include flex-basis(15em);
      @include flex-grow(2);
      @include transition (all 0.2s ease-in-out);
      background-color: $card-background;
      border-radius: $base-border-radius;
      border: $card-border;
      margin: 0 $card-margin $base-spacing 0;
      position: relative;

      .news-image {
        overflow: hidden;

        img {
          @include transition (all 0.3s ease-in-out);
          border-top-left-radius: $base-border-radius;
          border-top-right-radius: $base-border-radius;
          @include filter(grayscale(100%));
          width: 100%;

          &:focus,
          &:hover {
            @include filter(grayscale(0%)); } } } }

    .news-header {
      @include transition (all 0.2s ease-in-out);
      background-color: $card-header-background;
      border-bottom: $card-border;
      border-radius: $base-border-radius $base-border-radius 0 0;
      font-weight: bold;
      line-height: $base-line-height * 0.85;
      padding: ($base-spacing / 3) ($base-spacing / 2); }

    .news-copy {
      font-size: 0.9em;
      line-height: 1.5em;
      padding: 0 ($base-spacing / 2) ($base-spacing / 2) ($base-spacing / 2);

      p {
        margin: 0 0 ($base-spacing / 2); }

      i.fa {
        color: $red; }

      a {
        text-transform: uppercase;
        font-size: $base-font-size * 0.9; }

      .upvotes {
        margin-top: 0.25em; }

      .time {
        color: $medium-gray; } }

    &:active {
      background-color: $card-background;

      .news-header {
        background-color: $card-background; }

